import { useState, useEffect, useRef } from 'react';
import Feed from "./Feed"
import { Space } from "antd"
import CalendarReact from './CalendarReact';
import EventsComponent from './EventsComponent';
import AutoSuggest from './AutoSuggest';
import Iframe from './Iframe';

const customEvents = [
    {
      date: 'Wed Aug 02 2023',
      event: 'Meeting 1'
    },
    {
      date: 'Thu Aug 10 2023',
      event: 'Quarterly results'
    },
    {
      date: 'Sat Sep 09 2023',
      event: 'Meeting 2'
    }
  ]

function Home() {
  const [selectedDay, setSelectedDay] = useState(new Date());
  const [events, setEvents] = useState(null);
  const iframeRef = useRef(null);
  const [iframeContent, setIframeContent] = useState('');

  const resizeIFrameToFitContent = (iFrame) => {
    iFrame.width = iFrame.contentWindow.document.body.scrollWidth;
    iFrame.height = iFrame.contentWindow.document.body.scrollHeight;
  }
  
  const filteredEvents = customEvents.filter((event) => {
    const formattedDate = new Date(event.date).toDateString();
    return formattedDate === selectedDay.toDateString();
  });

  useEffect(() => {
    setEvents(customEvents);


    const handleDOMContentLoaded = () => {
      const iframeBody = iframeRef.current.contentWindow.document.body;
      console.log(iframeRef.current.contentWindow.document.body);
        resizeIFrameToFitContent(iframeRef);
    };

    window.addEventListener('DOMContentLoaded', handleDOMContentLoaded);

    return () => {
      window.removeEventListener('DOMContentLoaded', handleDOMContentLoaded);
    };
  }, [])

  const handleDayClick = (date) => {
    setSelectedDay(date);
  };

  return (
    <>
      {/* <AutoSuggest/>   */}
      {/* <button onClick={handleGetIframeContent}>Get Iframe Content</button>  */}
      {/* <iframe id='my-iframe' ref={iframeRef} src='/search'></iframe>   
      <pre>{iframeContent}</pre>
          <Space>
          <Feed/>
            <CalendarReact handleDayClick={handleDayClick} customEvents={customEvents}/>
            {/*<EventsComponent selectedDay={selectedDay} events={filteredEvents} /> */}
          {/* </Space> */}
        <Iframe/>
    </>
  )
}

export default Home
