import React, { useState } from 'react';
import { AutoComplete } from 'antd';
import { Link } from "react-router-dom";

const options = [
    {
      value: 'Burns Bay Road',
    },
    {
      value: 'Downing Street',
    },
    {
      value: 'Wall Street',
    },
  ];

function AutoSuggest() {
  const [dataSource, setDataSource] = useState(null);
  const [loading, setLoading] = useState(false);

  function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  const handleSearch = async (value) => {
    // Call your API endpoint with the search value and get the results.
    let thisVal = value;

    setLoading(true);
    try {
      const response = await fetch(`https://jsonplaceholder.typicode.com/todos/${getRandomInt(1, 20)}`);
      const results = await response.json(); // Assuming the response is an array of results.

      const resObject = {
        value: `${thisVal} ${results.title}`,
        label: (
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
              }}
            >
              <span>
                Found {results.title} on{' '}
                <Link to='/search'>
                  category
                </Link>
              </span>
              <span>{getRandomInt(200, 100)} results</span>
            </div>
          )
      }
      setDataSource(value ? [resObject] : []);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching data:', error);
      setDataSource([]); // Set an empty array if there's an error or no results.
    }
  };

  return (
    <>
      <h2>Search</h2>
      <AutoComplete
        onSearch={handleSearch}
        options={dataSource}
        placeholder="Search..."
      />
    </>
  )
}

export default AutoSuggest
