import React, { useRef, useEffect } from 'react';

function Iframe() {
  const iframeRef = useRef(null);

  useEffect(() => {
    const iframe = iframeRef.current;
    const updateIframeHeight = () => {
      if (iframe) {
        const iframeDocument = iframe.contentDocument || iframe.contentWindow.document;
        const iframeContentHeight = iframeDocument.documentElement.scrollHeight;
        const headerElement = iframeDocument.querySelector('header');
        console.log(iframeDocument);
        // Set the iframe's height
        iframe.style.height = iframeContentHeight + 'px';

        if (headerElement) {
            headerElement.style.display = 'none';
        }
      }
    };

    // Attach the event listener to adjust the height when the iframe content changes
    iframe.addEventListener('load', updateIframeHeight);
    
    // Initial adjustment
    updateIframeHeight();

    // Clean up the event listener when the component unmounts
    return () => {
      iframe.removeEventListener('load', updateIframeHeight);
    };
  }, []);

  return (
    <>
      <iframe 
        ref={iframeRef}
        //src='http://localhost:3002/custom/test-pierre.html'
        src='https://pierrejarry.github.io/page/' 
        style={{border: 'none', width: '100%'}}
      />
    </>
  )
}

export default Iframe
